import { Info } from "widgets/info";
import { delivery, cooperation } from "assets";
import cn from "classnames";

import styles from "./AboutPage.module.scss";
import { FC } from "react";

export const AboutPage: FC = () => {
  return (
    <div className="wrapper">
      <main className="container">
        <div className={styles.delivery}>
          <div
            className={cn(
              styles.payment__content,
              styles.content
            )}
          >
            <h2
              className={cn(
                styles.payment__title,
                styles.title
              )}
            >
                    
            </h2>
            <p
              className={cn(
                styles.payment__text,
                styles.text
              )}
            >
               !   -  
                   
                 .  i 
                 ii 
               ,   ,   
               .     !
            </p>
          </div>
          <img
            width="486"
            height="450"
            src={delivery}
            alt="      "
          />
        </div>
        <div className={styles.cooperation}>
          <div
            className={cn(
              styles.cooperation__content,
              styles.content
            )}
          >
            <h2
              className={cn(
                styles.cooperation__title,
                styles.title
              )}
            >
                 
            </h2>
            <p
              className={cn(
                styles.cooperation__text,
                styles.text
              )}
            >
                   
                 
              .       
                .   
               
              <a href="mailto:fruti.business@gmail.com">
                fruti.business@gmail.com
              </a>
            </p>
          </div>
          <img
            width="486"
            height="450"
            src={cooperation}
            alt="   "
          />
        </div>
        <Info />
      </main>
    </div>
  );
};
