import cn from "classnames";
import styles from "./DeliveryPage.module.scss";
import { delivery, payment } from "assets";
import { FC } from "react";

export const DeliveryPage: FC = () => {
  return (
    <div className="wrapper">
      <main className="container">
        <div className={styles.payment}>
          <div
            className={cn(
              styles.payment__content,
              styles.content
            )}
          >
            <h2
              className={cn(
                styles.payment__title,
                styles.title
              )}
            >
               
            </h2>
            <p
              className={cn(
                styles.payment__text,
                styles.text
              )}
            >
                   
                  
                  Privat24 
              Monobank.  i    
                  .
            </p>
          </div>
          <img
            width="486"
            height="450"
            src={payment}
            alt=" "
          />
        </div>
        <div className={styles.delivery}>
          <div
            className={cn(
              styles.delivery__content,
              styles.content
            )}
          >
            <h2
              className={cn(
                styles.delivery__title,
                styles.title
              )}
            >
              
            </h2>
            <p
              className={cn(
                styles.delivery__text,
                styles.text
              )}
            >
                 . , ,
                  11-20:00. 
               - 50 .    
               500   .
            </p>
          </div>
          <img
            width="486"
            height="450"
            src={delivery}
            alt=""
          />
        </div>
      </main>
    </div>
  );
};
