import { about1, about2, about3 } from 'assets';
import { FC } from 'react';

import styles from './About.module.scss';

export const About: FC = () => {
  return (
    <section id="about" className={styles.about}>
      <h2 className={(styles.about__title, 'visually-hidden')}> </h2>
      <ul className={styles.about__list}>
        <li className={styles.about__item}>
          <img
            src={about1}
            alt="  "
            className={styles.about__item_img}
          />
          <h2 className={styles.about__item_title}>  </h2>
          <p className={styles.about__item_text}>
                 .   . , ,
              
          </p>
        </li>
        <li className={styles.about__item}>
          <img
            src={about2}
            alt="   "
            className={styles.about__item_img}
          />
          <h2 className={styles.about__item_title}>   </h2>
          <p className={styles.about__item_text}>
               .      
          </p>
        </li>
        <li className={styles.about__item}>
          <img
            src={about3}
            alt="  "
            className={styles.about__item_img}
          />
          <h2 className={styles.about__item_title}>  </h2>
          <p className={styles.about__item_text}>
                     .
                  
          </p>
        </li>
      </ul>
    </section>
  );
};